@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

:root{
  --white: white;
  --background: #130f40;
  --navactive: #130f40;
}

[data-theme="light"] {
  --white: #2c3e50;
  --background: #dfe6e9;
  --navactive: #dfe6e9;
}

html{
  scroll-behavior: smooth;
  transition: .5s;
}

.body{
 background-color: var(--background); 
 transition: .5s ease-in-out;
}


// HEADER....................................................................


.navbar{
  .nav{
    display: flex;
    justify-content: space-between;
    height: 9vh;
    align-items: center;
    .logo{
      a{
        text-decoration: none;
        color: var(--white);
        font-size: 25px;
        font-weight: 600;
        .io-5{
          font-size: 45px;
          padding-bottom: 5px;
        }
      }
      @media screen and (max-width: 570px) {
        padding-left: 10px;
      }
    }
    .icons{
      display: none;
      @media screen and (max-width: 1000px) {
        display: block;
        color: var(--white);
        font-size: 22px;
        z-index: 10;
      }
      .time{
        color: #000;
      }
    }
    .nav-menu{
      display: flex;
      list-style-type: none;
      margin: 0;
      @media screen and (max-width: 1000px) {
        flex-direction: column;
        position: absolute;
        background-color: #bdc3c7;
        top: 0;
        right: -100%;
        width: 100%;
        padding: 0;
        // left: 0;
        height: 60vh;
        // border-radius: 5px;
        text-align: center;
        justify-content: center;
        border: 1px solid #dcdde1;
        z-index: 1;
      }
      .nav-items{
        @media screen and (max-width: 1000px) {
          padding: 13px 0;
        }
        a{
          color: var(--white);
          text-decoration: none;
          padding: 1rem;
          font-weight: 500;
          letter-spacing: 1px;
          &:hover{
            color: blue;
            transition: .5s;
          }
          @media screen and (max-width: 1000px) {
            font-size: 22px;
            color: #000;
          }
        }
      }
      .nav-items-btn{
        @media screen and (max-width: 1000px) {
          padding-top: 15px;
        }
        a{
          text-decoration: none;
          color: white;
          background-color: blue;
          padding: 10px 20px;
          border-radius: 5px;
          margin-right: 1rem;
          @media screen and (max-width: 1000px) {
            font-size: 20px;
          }
        }
      }
      .sun-moon{
        @media screen and (max-width: 1000px) {
            display: none;
        }
      }
    }
    .nav-menu.active{
      right: 0;
      transition: all .5s;
    }
    .fa-sun{
      color: yellow;
      font-size: 22px;
      cursor: pointer;
    }
    .fa-moon{
      color: black;
      font-size: 22px;
      cursor: pointer;
    }
  }
  .icon{
    display: flex;
    gap: 1rem;
    align-items: baseline;
    .modes{
      display: none;
      .fa-sun{
        color: yellow;
    }
    .fa-moon{
        color: black;
    }
      @media screen and (max-width: 991px) {
        display: block;
    }
    }
    
  }
}

.navbar-active{
  height: 12vh;
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  background-color: var(--navactive);
  box-shadow: -1px 11px 24px -11px rgba(0,0,0,0.75);
  transition: .5s;
}


// ABOUT............................................................................


.About{
    padding: 7rem 0;
  .about-content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
    width: 100%;
    @media screen and (max-width: 991px) {
      grid-template-columns: 1fr;
      text-align: center;
    }
    .left{
      .left-content{
        padding-top: 30px;
        @media screen and (max-width: 1200px) {
          padding-top: 0px;
        }
        h1{
          color: var(--white);
          font-size: 45px;
          font-weight: bold;
          @media screen and (max-width: 1200px) {
            font-size: 40px;
          }
          @media screen and (max-width: 482px) {
            font-size: 30px;
          }
          @media screen and (max-width: 370px) {
            font-size: 24px;
          }
          h2{
            transform: skewX(-10deg);
            padding-top: 1rem;
          }
          span{
            font-style: italic;
            font-size: 40px;
            font-weight: bold;
            background-color: blue;
            padding: 0 15px;
            color: white;
            @media screen and (max-width: 1200px) {
              font-size: 35px;
            }
            @media screen and (max-width: 482px) {
              font-size: 30px;
            }
            @media screen and (max-width: 370px) {
              font-size: 25px;
            }
          }
        }
        p{
          color: var(--white);
          padding-top: 2rem;
          font-weight: 500;
          font-size: 17px;
        }
        .btns{
          padding-top: 2rem;
          @media screen and (max-width: 370px) {
            display: flex;
            flex-direction: column;
          }
          .one{
            border: none;
            font-size: 20px;
            background-color: blue;
            color: #fff;
            padding: 10px 20px;
            border-radius: 5px;
            margin-right: 2.5rem;
            font-weight: 500;
            @media screen and (max-width: 482px) {
              font-size: 16px;
            }
            @media screen and (max-width: 370px) {
              margin: 0;
              margin-bottom: 1rem;
            }
          }
          .two{
            background-color: transparent;
            color: var(--white);
            font-weight: 500;
            border: none;
            font-size: 20px;
            @media screen and (max-width: 482px) {
              font-size: 18px;
            }
            i{
              padding-right: 6px;
            }
          }
        }
      }
    }

    .right{
      .image{
        max-width: 100%;
        img{
          width: 100%;
          border-radius: 30px;
        }
      }
    }
  }
}



.about-2{
  height: auto;
  padding: 4rem 0;

  .about-2-content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;
    @media screen and (max-width: 991px) {
      grid-template-columns: 1fr;
      text-align: center;
    }

    .right-2{
      .right-2-content{
        h6{
          color: var(--white);
          letter-spacing: 2px;
          opacity: .7;
        }
        h1{
          color: var(--white);
          font-weight: bold;
          font-size: 45px;
          @media screen and (max-width: 1200px) {
            font-size: 35px;
          }
          h2{
            transform: skewX(-10deg);
            padding: 1rem 0;
            span{
              padding: 0 10px;
              font-weight: bold;
              color: #fff;
              background-color: blue;
              font-style: italic;
              font-size: 45px;
              @media screen and (max-width: 1200px) {
                font-size: 35px;
              }
            }
          }
        }
        p{
          color: var(--white);
          font-weight: 500;
          padding: .7rem 0;
        }
        button{
          border: none;
          background-color: blue;
          color: #fff;
          padding: 10px 20px;
          border-radius: 5px;
        }
      }
    }
    .left-2{
      .image-2{
        width: 100%;
        img{
          width: 100%;
          border-radius: 30px;
        }
      }
    }
  }
}



// PRICING..................................................................... 


.pricing-2{
  height: auto;
  // padding-top: 25rem;
  .pricing-2-content{
    
    h1{
      color: var(--white);
      text-align: center;
      font-size: 3.5rem;
      font-weight: bold;
      padding-top: 10rem;
      span{
        font-style: italic;
        color: #fff;
        background-color: blue;
        padding: 0 13px;
        a{
          transform: skewX(-10deg);
          text-decoration: none;
          color: #fff;
        }
      }
    }
    .box{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 4rem;
      padding: 6rem 0;
      @media screen and (max-width: 991px) {
        grid-template-columns: 1fr;
      }
      .card{
        text-align: center;
        // position: relative;
        padding: 20px 0;
        background-color: transparent;
        cursor: pointer;
        &:hover{
          box-shadow: 0 0 2px rgba(0,0,0,0.75);
          transform: scale(1.1);
          transition: .5s ease-in-out;
        }
        img{
          width: 60%;
          @media screen and (max-width: 991px) {
            width: 30%;
          }
        }
        h3{
          color: var(--white);
          font-size: 32px;
          font-weight: 600;
          @media screen and (max-width: 1200px) {
            font-size: 25px;
          }
        }
        p{
          color: var(--white);
          font-size: 18px;
        }
        a{
          text-decoration: none;
          color: var(--white);
          font-weight: 600;
          font-size: 20px;
          @media screen and (max-width: 1200px) {
            font-size: 18px;
          }
        }
      }
    }
  }
}

.Pricing{
  h1{
    color: var(--white);
    font-size: 52px;
    font-weight: bold;
    text-align: center;
    padding: 5rem 0;
    @media screen and (max-width: 768px) {
      font-size: 46px;
    }
    @media screen and (max-width: 530px) {
      font-size: 40px;
    }
    @media screen and (max-width: 463px) {
      font-size: 30px;
    }
    span{
      background-color: blue;
      padding: 0 10px;
      font-style: italic;
      color: white;
    }
  }
  .cards{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2.5rem;
    @media screen and (max-width: 991px) {
      grid-template-columns: repeat(2,1fr);
    }
    @media screen and (max-width: 570px) {
      grid-template-columns: repeat(1,1fr);
    }
    .card{
      max-height: 100%;
      border: none;
      cursor: pointer;    
      box-shadow: 0 0 2px 0 rgba(0,0,0,0.7);
      .card-body{
        background-color: #c8d6e5;
        padding: 0px;
        .card-content{
          padding: 10px 20px;
          @media screen and (max-width: 1200px) {
            padding: 9px 9px;
          }
          h5{
            font-size: 24px;
            font-weight: 600;
            @media screen and (max-width: 1400px) {
              font-size: 22px;
            }
            @media screen and (max-width: 1200px) {
              font-size: 18px;
              font-weight: 600;
            }
          }
          p{
            opacity: .8;
          }
          h4{
            font-weight: bold;
          }
        }
      }
    }
  }
}



// BLOG...................................................................



.Blog{
  height: auto;
  padding-top: 8rem;
  .blog-content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    @media screen and (max-width: 991px) {
      grid-template-columns: 1fr;
      text-align: center;
    }
    .left{
      h6{
        color: var(--white);
        opacity: .7;
        letter-spacing: 2px;
      }
      h1{
        color: var(--white);
        font-weight: bold;
        font-size: 52px;
        padding: 20px 0;
        @media screen and (max-width: 1200px) {
          font-size: 40px;
        }
        @media screen and (max-width: 391px) {
          font-size: 30px;
        }
        span{
          font-style: italic;
          color: #fff;
          background-color: blue;
          padding: 0px 10px;
        }
      }
      p{
        color: var(--white);
        font-size: 20px;
        @media screen and (max-width: 1200px) {
          font-size: 17px;
        }
        @media screen and (max-width: 391px) {
          font-size: 13px;
        }
      }
      .divs{
        display: flex;
        justify-content: space-between;
        width: 65%;
        padding-bottom: 20px;
        @media screen and (max-width: 991px) {
          width: 100%;
          justify-content: center;
        }
        h2{
          color: var(--white);
          font-weight: 600;
          @media screen and (max-width: 1200px) {
            font-size: 23px;
          }
        }
        h5{
          color: var(--white);
          @media screen and (max-width: 1200px) {
            font-size: 16px;
          }
        }
      }
      button{
        border: none;
        background-color: blue;
        color: #fff;
        padding: 10px 20px;
        border-radius: 5px;
      }
    }
    .right{
      .image{
        width: 100%;
        img{
          width: 100%;
          border-radius: 20px;
        }
      }
    }
  }
}








.customers{
  height: auto;
  padding: 10rem 0;
  .customers-content{
    text-align: center;
    h1{
      color: var(--white);
      font-size: 52px;
      font-weight: bold;
      @media screen and (max-width: 768px) {
        font-size: 42px;
      }
      @media screen and (max-width: 466px) {
        font-size: 32px;
      }
      @media screen and (max-width: 366px) {
        font-size: 26px;
      }
      span{
        color: #fff;
        font-style: italic;
        background-color: blue;
        padding: 0 10px;
      }
    }
    .box{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 3rem;
      @media screen and (max-width: 991px) {
        grid-template-columns: 1fr;
      }

      .cards{
        margin-top: 6rem;
        padding: 1rem;
        cursor: pointer;
        border-radius: 5px;
        &:hover{
          box-shadow: 0 0 20px 10px rgba(0,0,0,0.75);
          transition: .5s ease-in-out;
        }
        img{
          width: 30%;
          border-radius: 50%;
        }
        .cards-content{
          padding: 50px 0;
          p{
            color: var(--white);
            font-size: 20px;
            opacity: .8;
            @media screen and (max-width: 1200px) {
              font-size: 17px;
            }
          }
        }
        h3{
          color: var(--white);
          font-weight: 700 ;
          font-size: 24px;
          letter-spacing: 2px;
          @media screen and (max-width: 1200px) {
            font-size: 19px;
          }
        }
      }
    }
  }
}



// CONTACT-US...........................................................................


.Contact{
  height: auto;
  background-color: #30336b;
  padding: 4rem 0;
  .contact-content{
    display: grid;
    grid-template-columns: 2fr 1fr;
    @media screen and (max-width: 991px) {
      grid-template-columns: 1fr;
      text-align: center;
    }

    .left{
      padding-top: 12rem;
      @media screen and (max-width: 991px) {
        padding: 5rem 0;
      }
      h5{
        color: yellow;
        letter-spacing: 1px;
      }
      p{
        color: #fff;
        font-size: 30px;
        font-weight: bold;
        padding: 20px 0;
        @media screen and (max-width: 768px) {
          font-size: 25px;
        }
        span{
          font-style: italic;
          color: blue;
          background-color: #c8d6e5;
        }
      }
      .butns{
        button{
          border: none;
          background-color: #dcdde1;
          margin-right: 1rem;
          padding: 20px 45px;
          border-radius: 40px;
          font-weight: 600;
          letter-spacing: 1px;
          @media screen and (max-width: 768px) {
            padding: 16px 37px;
          }
          @media screen and (max-width: 493px) {
            margin: 1rem 0;
            padding: 14px 33px;
          }
        }
        @media screen and (max-width: 493px) {
          display: flex;
          flex-direction: column;
        }
      }
    }
    .right{
      .image{
        img{
          width: 100%;
          @media screen and (max-width: 991px) {
            width: 50%;
          }
        }
      }
    }
  }
}





.footer{
  .footer-content{
    display: grid;
    grid-template-columns: 2fr 1fr;
    padding: 5rem 0;
    @media screen and (max-width: 1200px) {
      grid-template-columns: 1fr;
      text-align: center;
    }
    .wrapper{
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      @media screen and (max-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media screen and (max-width: 360px) {
        grid-template-columns: repeat(1, 1fr);
      }  
    }
    h4{
      color: var(--white);
      font-weight: bold;
      letter-spacing: 1px;
    }
    ul{
      list-style-type: none;
      padding: 0;
      padding-top: 10px;
      @media screen and (max-width: 1400px) {
        padding-top: 40px;
      }
      @media screen and (max-width: 768px) {
        padding-top: 0px;
      }
      li{
        color: var(--white);
        font-size: 18px;
        line-height: 43px;
      }
    }
    p{
      color: var(--white);
      font-size: 18px;
      padding: 20px 0; 
    }
    .form{
      @media screen and (max-width: 1200px) {
        padding-top: 4rem;
      } 
    }
    input{
      padding: 10px 20px;
    }
    span{
      background-color: blue;
      border: none;
      color: #dcdde1;
      font-size: 18px;
      letter-spacing: 1px;
      font-weight: 500;
      cursor: pointer;
    }
  }
}



.footer-2{
  padding: 6rem 0;
  .footer-2-content{
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: 991px) {
      flex-direction: column;
      text-align: center;
    } 
    .foot-logo{
      color: var(--white);
      font-size: 22px;
      font-weight: bold;
      letter-spacing: 1px;
    }
    .center{
      color: var(--white);
      font-size: 22px;
      opacity: .7;
      @media screen and (max-width: 991px) {
        padding: 2rem 0;
      }
    }
    .foot-icons{
      color: #dcdde1;
      font-size: 20px;
      .fab{
        margin-right: 1rem;
        padding: 10px 14px;
        border-radius: 50%;
        background-color: blue;
      }
    }
  }
}